.container-error{
   width:100%;
   height: 100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}

.container-error img{
   max-width: 300px;
   margin: 24;
   animation: shanke 2s;
}

@keyframes shanke {
   10%, 90%{

      transition: translate3d(-1px, 0, 0)

   }
   20%, 80%{
      transform: translate3d(2px, 0, 0)
   }

   30%, 50%, 70%{
      transform: translate3d(-4px, 0, 0)
   }
   40%, 60%{
      transform: translate3d(4px, 0, 0)
   }
   
}

.container-error h1{
   color:#fff;

}

.container-error a{

   color:black;
   background-color: #fff;
   text-decoration: none;
   padding: 12px 18px;
   border-radius: 4px;
   margin: 14px 0;
   font-weight: bold;
}