.container-home{
   width: 100%;
   height:100vh;
   display: flex;
   justify-content: center;
   align-items: center;
   flex-direction: column;
}

.logo{
   display: flex;
   flex-direction: column;
   align-items: center;
}

.logo h1{
   font-size: 60px;
   color: #fff;

}

.logo span{
   color:#fff;
   margin-top:  14px;
   margin-bottom: 34px;
   font-size: 19px;
}

.area-input{
   width: 540px;
   display: flex;
   flex-direction: column;
   align-items: center;
   animation: slideY 2s;
}
@keyframes slideY {
      from{
         transform: translateY(90px)
      }
      top{
         transform: translateY(0px)
      }
   
}

.area-input div {
   width: 100%;
   height:46px;
   background-color: rgb(255, 255, 255, 0.15);
   display: flex;
   align-items: center;
   justify-content: center;
   border-radius: 4px;
}


.area-input svg{
   margin:0 8px;

}

.area-input input{
   width: 100%;
   background:transparent;
   border: 0;
   height: 46px;
   color: #fff;
   outline:none;
   font-size: 18px;
}

.area-input input::placeholder{
   color:rgb(255, 255, 255, 0.8)

}
.area-input button{
   width: 100%;
   height:46px;
   border-radius:  4px;
   border:0;
   font-size: 16px;
   font-weight: bold;
   margin-top:  8px;
   transition: transform 0.6s;
}

.area-input button:hover{ 
   transform: scale(1.05);
}


@media(max-width: 615px){
   .logo img{
      width: 95px;

   }
   .logo h1{ 
      font-size: 40px;

   }
   .logo span{ 
      font-size: 16px;
   }
   .area-input{
      width:85%;
   }

}

